import React, {useState} from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import './Coin.css';
import axios from 'axios';
import Popup from 'reactjs-popup';

const CoinTd = styled.td`
    border: 1px solid #cccccc;
    width: 40vh;
`;

const Button = styled.button`
    font-size: 1 rem;
    background-color: aqua;
    border-radius: 10px;
    cursor: pointer;
`;

const CoinButton = styled.button`
    font-size: 1 rem;
    width: 17vw;
`;

export default function Coin(props) {

    const [openModal, setOpenModal] = useState(false);
    const closeModal = () => setOpenModal(false);
    const [handleCoinModal, setCoinModal] = useState('');
    //const [handleOHLCModal, setOHLCModal] = useState('');

    const handleClick = (event) => {
        // prevent the default action of submitting the form
        event.preventDefault();
        props.handleRefresh(props.id);
    }

    const handleBuyTrade = (event) => {
        event.preventDefault();
        props.handleTrading(true, props.id);
    }

    const handleSellTrade = (event) => {
        event.preventDefault();
        props.handleTrading(false, props.id);
    }

    const HandleCoinModal = async (event) => {
        event.preventDefault();
        setOpenModal(open => !open);
        const url = `https://api.coinpaprika.com/v1/coins/${props.id}`;
        const response = await axios.get(url);
        const coinDescription = response.data.description;
        setCoinModal(coinDescription);
    }

    // const OHLCChart = async (event) => {
    //     event.preventDefault();
    //     setOpenModal(open => !open);
    //     const url = `https://api.coinpaprika.com/v1/coins/${props.id}/ohlcv/today/`;
    //     const response = await axios.get(url);
    //     const ohlc = response.data;
    //     setOHLCModal(ohlc);
    //     return [{
    //         "open": ohlc.open,
    //         "high": ohlc.high,
    //         "low": ohlc.low,
    //         "close": ohlc.close
    //     }];
    // }

    let balance = '*****';
    if (props.showBalance) {
        balance =
            <CoinTd>
                <span>{props.balance}</span>
            </CoinTd>
    }

    return (
        <>
        <tr>
            <CoinTd>
                <CoinButton
                    className="btn btn-outline-info"
                    onClick={HandleCoinModal}>
                    {props.name}
                </CoinButton>
            </CoinTd>
            <Popup
                open={openModal}
                closeOnDocumentClick
                onClose={closeModal}>
                    <div className="popup">
                    <button className="close" onClick={closeModal}>
                        &times;
                    </button>
                        {handleCoinModal}
                    </div>
            </Popup>
            <CoinTd>{props.ticker}</CoinTd>
            <CoinTd>${props.price}</CoinTd>
            {balance}
            <CoinTd>
                <form action="#" method="POST">
                    <Button className="btn btn-outline-primary" onClick={handleClick}>Refresh</Button>
                    <Button className="btn btn-outline-success" onClick={handleBuyTrade}>Buy</Button>
                    <Button className="btn btn-outline-danger" onClick={handleSellTrade}>Sell</Button>
                </form>
            </CoinTd>
        </tr>
        </>
    );
}

Coin.propTypes = {
    name: PropTypes.string.isRequired,
    ticker: PropTypes.string.isRequired,
    price: PropTypes.number.isRequired
}
