import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

const Section = styled.section`
    font-size: 2rem;
    text-align: left;
    padding: 1.5rem 0 1.5rem 5rem;
`;

const Button = styled.button`
    font-size: 1.3rem;
    margin: 1.5rem 0 1.5rem 5rem;
    background-color: aqua;
    border-radius: 5px;
    cursor: pointer;
`;

export default function AccountBalance(props) {
    
    let buttonText = 'Show Balance';
    let balance = '*****';
    let addFunds = null;

    if (props.showBalance) {
        buttonText = 'Hide Balance';
        balance = <span><strong>Balance:</strong> ${ props.amount.toFixed(2) }</span>;
        addFunds = <Button 
                        className="btn btn-outline-primary" 
                        onClick={props.handleAddFunds}>Add To Balance
                        <i className="fas fa-helicopter"></i>
                    </Button>;
    }

    return (
        <Section>
            {balance}
            <Button className="btn btn-outline-primary" 
                onClick={props.handleShowBalance}>{buttonText}</Button>
            {addFunds}
            <Button className="btn btn-outline-primary"
                onClick={() => window.location.reload(false)}>Lazy Refresh</Button>
        </Section>
    );
}


AccountBalance.propTypes = {
    amount: PropTypes.number.isRequired
}
