import React, { useEffect, useState } from 'react';
import AccountBalance from './components/AccountBalance/AccountBalance';
import CoinList from './components/CoinList/CoinList';
import TitleList from './components/TitleList/TitleList';
import TitleFooter from './components/TitleFooter/TitleFooter';
import styled from 'styled-components';
import axios from 'axios';
import 'bootswatch/dist/cyborg/bootstrap.min.css';
import '@fortawesome/fontawesome-free/js/all';

const Div = styled.div`
  text-align: center;
  background-color: black;
  color: #cccccc;
`;

const formatPrice = price => parseFloat(Number(price).toFixed(2));

function App(props) {
  const [coinCount] = useState(10);
  const [balance, setBalance] = useState(50000);
  const [showBalance, setShowBalance] = useState(false);
  const [coinData, setCoinData] = useState([]);

  const componentDidMount = async () => {
    const response = await axios.get('https://api.coinpaprika.com/v1/coins')
    const coinIds = response.data.slice(0, coinCount).map(coin => coin.id);
    const tickerUrl = 'https://api.coinpaprika.com/v1/tickers/';
    const promises = coinIds.map(id => axios.get(tickerUrl + id));
    const coinData = await Promise.all(promises);
    const coinPriceData = coinData.map(function(response) {
      const coin = response.data;
      return {
        key: coin.id,
        name: coin.name,
        ticker: coin.symbol,
        balance: 0,
        price: formatPrice(coin.quotes['USD'].price)
      };
    });
    //retrieve the prices
    setCoinData(coinPriceData);
  }

  useEffect(function() {
    if (coinData.length === 0) {
      componentDidMount();
    }
  });

  const handleRefresh = async (valueChangeKey) => {
    const tickerUrl = `https://api.coinpaprika.com/v1/tickers/${valueChangeKey}`;
    const newValue =  await axios.get( tickerUrl );
    const responses = coinData.map(function(values) {
      let newValues = { ...values };
      if (valueChangeKey === values.key) {
        newValues.price = formatPrice(newValue.data.quotes['USD'].price);
      }
      return newValues;
    });
    setCoinData(responses);
  }

  const handleShowBalance = () => {
    setShowBalance(oldValue => !oldValue);
  }

  const handleAddFunds = () => {
    setBalance(oldValue => oldValue + 1200);
  }

  const handleTrading = (isBuy, valueChangeKey) => { 
    const balanceShift = isBuy ? 1 : -1; //1 = buy, -1 = sell
    const responses = coinData.map(function(values) {
      let newValues = { ...values };
      if (valueChangeKey === values.key) {
        newValues.balance += balanceShift;
        setBalance(oldBalance => oldBalance - balanceShift * newValues.price);
      }
      return newValues;
    });    
    setCoinData(responses);
  }

  return (
    <Div>
      <TitleList />
      <AccountBalance amount={balance} 
                      showBalance={showBalance} 
                      handleShowBalance={handleShowBalance}
                      handleAddFunds={handleAddFunds} />
      <CoinList coinData={coinData} 
                handleRefresh={handleRefresh}
                showBalance={showBalance}
                handleTrading={handleTrading} />
      <TitleFooter />
    </Div>
  );
}

  

export default App;
